.header {
  text-align: center;
  font-family: Roboto, sans-serif;
}

.header h3 {
  font-style: italic;
  font-weight: normal;
}

.cal-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  display: flex;
}

.cal-container .calendar {
  border-collapse: collapse;
  border: .5px solid #000;
  margin: 1%;
}

.cal-container .calendar .month-name {
  text-align: center;
  margin: 0;
}

.cal-container .calendar table, .cal-container .calendar th, .cal-container .calendar td {
  border-collapse: collapse;
}

.cal-container .calendar th, .cal-container .calendar td {
  text-align: center;
  width: 2em;
  height: 2em;
}

.cal-container .legend {
  width: 15%;
}

.cal-container .legend .square {
  aspect-ratio: 1;
  width: 50%;
}

.grade {
  text-align: center;
  font-family: Roboto, sans-serif;
  font-size: xx-large;
}

.grade .letter {
  font-weight: bold;
}

.grade .percent {
  font-weight: normal;
}

.makeups {
  white-space: pre-wrap;
  justify-self: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  display: flex;
}

.disclaimer {
  text-align: center;
  font-family: Roboto, sans-serif;
  font-style: italic;
}
